                            text-align     : center;
                            content        : "\f040";
                            color          : #fff;
                            background     : #096;
                            border-radius  : 50%;
                            font-family    : FontAwesome;
                            font-weight    : normal;
                            text-align     : left;
                            font-style     : normal;
                            display        : inline-block;
                            text-decoration: inherit;
                        }
                    }

                    a {
                        position  : absolute;
                        top       : 0;
                        left      : 0;
                        width     : 100%;
                        height    : 100%;
                        background: #fff;
                        opacity   : 0;
                    }
                }

                .media-cell-desc {
                    padding      : 28px 40px;
                    border-style : solid;
                    border-width : 1px;
                    background   : #fff;
                    border-color : #EAEDEF;
                    border-radius: 0 0 4px 4px;

                    .date {
                        float        : left;
                        margin-right : 16px;
                        padding      : 6px 7px;
                        text-align   : center;
                        font-size    : 14px;
                        line-height  : 1;
                        color        : #fff;
                        border-radius: 2px;
                        margin-top   : -66px;
                        position     : relative;
                        background   : #FFF;
                        margin-left  : 0;
                        background   : #5BCF80;

                        .day {
                            display       : block;
                            font-size     : 26px;
                            letter-spacing: -1px;
                            font-weight   : 800;
                            color         : #fff;
                        }
                    }

                    h3 {
                        text-transform: uppercase;
                        padding       : 0;
                        margin        : 0;
                        font-weight   : 700;
                        font-size     : 14px;
                        letter-spacing: 0.03em;
                    }

                    a {
                        color        : #444;
                        border-bottom: 2px solid #FFF;
                        transition   : color 0.1s, background-color 0.2s, border-color 0.2s;
                    }
                }
            }
        }

        .launch {
            text-align: center;
            padding   : 2.6em 0;
            text-align: center;
            padding   : 2.6em 0;

            .button {
                padding       : 1.8em 3.6em;
                line-height   : 1;
                display       : inline-block;
                position      : relative;
                padding       : 1.3em 2.6em;
                font-size     : 11px;
                letter-spacing: 1px;
                font-weight   : 800;
                text-transform: uppercase;
                line-height   : 1.2;
                border        : 0;
                outline       : 0;
                background    : #fff;
                border        : 2px solid #6A7686;
                color         : #6A7686;
                text-shadow   : none;
                border-radius : 4px;
            }
        }

        .center {
            text-align: center;
        }
    }
}
